@import "monokai";
@import url('https://fonts.googleapis.com/css?family=Lato:300italic,700italic,300,700');
@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:300italic,700italic,300,700');
@import url('https://fonts.googleapis.com/css?family=Nunito+Sans:300italic,700italic,300,700');
@import url('https://fonts.googleapis.com/css?family=Oxygen:300italic,700italic,300,700');


html {
  background:#5F5B6B;
  background: #5F5B6B linear_gradient(rgb(147, 142, 160), #5F5B6B) fixed;
}

body {
  padding:50px 0;
  margin:0;
  font:14px/1.5 Josefin Sans, "Helvetica Neue", Helvetica, Arial, sans-serif;
  color:#0B3142;
  font-weight:300;

}

.wrapper {
  width:640px;
  margin:0 auto;
  background:#D6D3F0;
  border-radius: 8px;
  box-shadow: rgba(#000, 0.2) 0 0 0 1px, rgba(#000, 0.45) 0 3px 10px;
}

header, section, footer {
  display:block;
}

a {
  color:#CA054D;
  text-decoration:none;
}

p {
  margin:0 0 20px;
  padding:0;
  .thick {
    font-weight: bold;
  }
}

strong {
  color:#222;
  font-weight:700;
}

header {
  border-radius: 8px 8px 0 0;
  background:#D7FDF0;
  background: linear_gradient(#D7FDF0, rgb(109, 153, 138));
  position:relative;
  padding:5px 30px 25px 30px;
  border-bottom:1px solid rgb(249, 217, 255);

  h1 {
    margin:0;
    padding:0;
    font-size:24px;
    line-height:1.2;
    color:#0F5257;

  
    nav {
      float: right;
      font-size: 15px;

      .current {
        color: black;
      }

    }
  
  }

  &.without-description h1 {
    margin:10px 0;
  }

  p {
    margin:0;
    color:#D6D3F0;
    width:300px;
    font-size:13px;

    &.view {
      display:none;
      font-weight:700;
      text-shadow:rgba(rgb(249, 217, 255), 0.9) 0 1px 0;
      -webkit-font-smoothing:antialiased;

      a {
        color:#06c;
      }

      small {
        font-weight:400;
      }
    }
  }

  ul {
    margin:0;
    padding:0;
    list-style:none;
    position:absolute;
    z-index:1;
    right:20px;
    top:20px;
    height:38px;
    padding:1px 0;
    background:#5198DF;
    background: linear_gradient(#77B9FB, #3782CD);
    border-radius:5px;
    box-shadow: inset rgba(#fff, 0.45) 0 1px 0, inset rgba(#000, 0.2) 0 -1px 0;
    width:auto;

    &:before {
      content:'';
      position:absolute;
      z-index:-1;
      left:-5px;
      top:-4px;
      right:-5px;
      bottom:-6px;
      background:rgba(#000, 0.1);
      border-radius: 8px;
      box-shadow: rgba(#000, 0.2) 0 -1px 0, inset rgba(#fff, 0.7) 0 -1px 0;
    }

    li {
      width:79px;
      float:left;
      border-right:1px solid #3A7CBE;
      height:38px;

      &.single {
        border:none;
      }
    }

    li + li {
      width:78px;
      border-left:1px solid #8BBEF3;
    }

    li + li + li {
      border-right:none;
      width:79px;
    }

    a {
      line-height:1;
      font-size:11px;
      color:#fff;
      color:rgba(#fff, 0.8);
      display:block;
      text-align:center;
      font-weight:400;
      padding-top:6px;
      height:40px;
      text-shadow:rgba(#000, 0.4) 0 -1px 0;

      strong {
        font-size:14px;
        display:block;
        color:#fff;
        -webkit-font-smoothing:antialiased;
      }
    }
  }
}

section {
  padding:15px 20px;
  font-size:15px;
  border-top:1px solid #fff;
  background: linear_gradient(#fafafa, #DEDEDE 700px);
  border-radius: 0 0 8px 8px;
  position:relative;

}


h1, h2, h3, h4, h5, h6 {
  color:#222;
  padding:0;
  margin:0 0 20px;
  line-height:1.2;
}

p, ul, ol, table, pre, dl {
  margin:0 0 20px;
}


h1, h2, h3 {
  line-height:1.1;
}

h1 {
  font-size:28px;
}

h2 {
  color:#393939;
}

h3, h4, h5, h6 {
  color:#494949;
}

li {
  &.nomarker {
    list-style-type:none;
    position: relative;
    left: -40px;
  }
}

blockquote {
  margin:0 -20px 20px;
  padding:15px 20px 1px 40px;
  font-style:italic;
  background:#ccc;
  background:rgba(#000, 0.06);
  color:#222;
}

figure {
  margin-left: 0px;
  margin-right: 0px;
  & pre {
    padding: 15px;
  }
  .lineno {
    padding: 5px;
  }
}


img {
  max-width:100%;
}

code, pre {
  font-family:Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal;
  color:#333;
  font-size:12px;
  overflow-x:auto;
}

pre {
  padding:20px;
  background: #3A3C42;
  color:#f8f8f2;
  margin:0 -20px 20px;

  code {
    color:#f8f8f2;
  }

  li & {
    margin-left:-60px;
    padding-left:60px;
    
  }
}

table {
  width:100%;
  border-collapse:collapse;
}

th, td {
  text-align:left;
  padding:5px 10px;
  border-bottom:1px solid #aaa;
}

dt {
  color:#222;
  font-weight:700;
}

th {
  color:#222;
}

small {
  font-size:11px;
}

hr {
  border:0;
  background:#aaa;
  height:1px;
  margin:0 0 20px;
}

footer {
  width:640px;
  margin:0 auto;
  padding:20px 0 0;
  color:#ccc;
  overflow:hidden;

  a {
    color:#fff;
    font-weight:bold;
  }

  p {
    float:left;
  }

  p + p {
    float:right;
  }
}

@media print, screen and (max-width: 740px) {
  body {
    padding:0;
  }

  .wrapper {
    border-radius: 0;
    box-shadow: none;
    width:100%;
  }

  footer {
    border-radius: 0;
    padding:20px;
    width:auto;

    p {
      float:none;
      margin:0;
    }

    p + p {
      float:none;
    }
  }
}

@media print, screen and (max-width:580px) {
  header ul {
    display:none;
  }

  header p.view {
    display:block;
  }

  header p {
    width:100%;
  }
}

@media print {
  header p.view a small:before {
    content:'at https://github.com/';
  }
}
